/*Media*/
a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  color: #000000; }

.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  max-width: 1440px;
  margin: 0 auto; }

.page-header {
  background-image: url("../../img/header_bike_desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin-bottom: 2495px; }
  @media (max-width: 1023px) {
    .page-header {
      background-image: url("../../img/header_bike_tablet.jpg"); } }
  @media (max-width: 767px) {
    .page-header {
      background-image: url("../../img/header_bike_mobile.jpg"); } }
  @media (max-width: 1023px) {
    .page-header {
      margin-bottom: 2352px; } }
  @media (max-width: 767px) {
    .page-header {
      margin-bottom: 3000px; } }

.page-header__wrapper {
  max-width: 880px;
  height: 825px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      height: min-content; } }

.page-header__logo {
  position: absolute;
  top: 318px; }
  @media (max-width: 1023px) {
    .page-header__logo {
      position: relative;
      top: 25px;
      padding-left: 21px; } }

.promo {
  display: flex;
  justify-content: space-between;
  padding-top: 251px;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .promo {
      flex-direction: column;
      align-items: center;
      position: relative;
      padding-top: 0; } }

.promo__text {
  max-width: 400px;
  padding-top: 56px; }
  @media (max-width: 1023px) {
    .promo__text {
      text-align: center;
      max-width: 525px;
      padding-top: 84px;
      margin-bottom: 80px; } }
  @media (max-width: 767px) {
    .promo__text {
      padding-top: 70px;
      margin: 0 16px 70px; } }

.promo__title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px; }
  @media (max-width: 1023px) {
    .promo__title {
      font-size: 30px;
      line-height: 42px; } }

.promo__description {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  opacity: 0.8; }
  @media (max-width: 1023px) {
    .promo__description {
      font-size: 18px;
      padding: 0 25px; } }
  @media (max-width: 767px) {
    .promo__description {
      padding: 0; } }

.promo__form {
  width: 327px; }
  @media (max-width: 1023px) {
    .promo__form {
      position: absolute;
      top: 290px;
      width: 321px;
      margin-top: 70px; } }
  @media (max-width: 767px) {
    .promo__form {
      top: 340px;
      width: 288px; } }

.main-nav {
  position: relative; }
  @media (max-width: 1023px) {
    .main-nav {
      height: 50px; } }

.main-nav__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 2;
      display: block;
      width: 50px;
      height: 50px;
      background-color: #0ad9c6;
      cursor: pointer;
      border: none; } }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 13px;
  width: 23px;
  height: 1px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000; }

.main-nav--opened .main-nav__toggle {
  z-index: 20;
  background-color: #ffffff; }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 25px;
    left: 8px;
    width: 34px;
    height: 1px;
    background-color: rgba(20, 21, 23, 0.2); }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg); }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  margin: 0;
  padding: 21px 25px 9px 21px;
  list-style: none; }
  .main-nav__list::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.2); }
    @media (max-width: 1023px) {
      .main-nav__list::before {
        display: none; } }

.main-nav__item {
  font-weight: 300; }
  @media (max-width: 1023px) {
    .main-nav__item {
      margin-bottom: 30px;
      padding: 17px 16px;
      background-color: #f6f6f6; } }

.main-nav__link {
  color: #ffffff;
  opacity: 0.7; }
  @media (max-width: 1023px) {
    .main-nav__link {
      color: #48494d; } }

@media (max-width: 1023px) {
  .main-nav--closed .main-nav__item {
    display: none; }
  .main-nav--opened .main-nav__list {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    padding: 100px 0;
    z-index: 2;
    background-color: #ffffff;
    position: absolute; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  /*.main-nav--nojs .main-nav__list {
    position: static;
    height: min-content;
  }

  .main-nav--nojs .main-nav__item {
    display: block;
  }*/ }

.user-form__title {
